/*navigationbar*/
.navbar {
  position: fixed;
  z-index: 2;
  top: 15%;
  right: 6%;
}

@media screen and (max-width: 800px) {
  .navbar{
    display: none;
    }
}

.navbar ul {
  list-style-type: none;
  width: 80px;
  display:inline-block;
  /*cursor:pointer;*/
  color:#666666;
  /*font-family:Arial;*/
  font-size:15px;
  /*font-weight:bold;*/
  /*padding:12px 20px;*/
  text-align: center;
}

.navbar li a {
  color: white;
  max-width: 30px; 
  text-decoration: none;
  font-size: 17px;
  font-family: Century Gothic,CenturyGothic,AppleGothic,sans-serif;
  font-variant: normal;
  font-weight: 10; /* For Safari 3.1 to 6.0 */
  transition: font-weight 100ms, font-size 100ms;
}

.navbar a:hover {
  font-weight : bold;
  font-size : 20px;
}

.is-active {
  font-weight : bold;
  font-size : 22px;
}

#hr {
  margin: 0 auto;
  margin-bottom: 45px;
  margin-top: 45px;
  width: 60%;
}


/*endnavigationbar*/
/*transitions*/
.content_1-transition, .content_2-transition, .content_3-transition , .content_4-transition {
  opacity: 0;
  transform: translateX(-40px);
  transition: all 0.5s ease-in-out;
}

.contentTransitionRight {
  opacity: 0;
  transform: translateX(40px);
  transition: all 0.5s ease-in-out;
}

.is-showing-right {
  opacity: 1 ;
  transform: translateX(-20px);
}

.is-showing {
  opacity: 1 ;
  transform: translateX(30px);
}

@media screen and (max-width: 1280){
  .content_1-transition, .content_2-transition, .content_3-transition , .content_4-transition {
    opacity: 0;
    transform: translateX(-20px);
    transition: all 0.5s ease-in-out;
  }

  .contentTransitionRight {
    opacity: 0;
    transform: translateX(40px);
    transition: all 0.5s ease-in-out;
  }

  .is-showing-right {
    opacity: 1 ;
    transform: translateX(-20px);
  }

  .is-showing {
    opacity: 1 ;
    transform: translateX(10px);
  }
}
/*end transitions*/

